.swiper {
  width: 100%;
  height: 100%;

  &-slide {
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;

    &__img,
    &__img-mobile {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    &__img-mobile {
      display: none;
    }

    &__img-box {
      position: relative;
      width: 100%;
      height: 90%;
      margin-bottom: 10px;
      margin-inline: 24px;
      padding-inline: 16px;
    }

    &__main-arrow {
      margin: 0;
      position: absolute;
      top: 0;
      width: 32px;
      height: 100%;
      border: 1px solid #B4BDC3;
      border-radius: 48px;
      background-color: #fff;
      background-repeat: no-repeat;
      background-position: center;
      cursor: pointer;
      z-index: 99;
      transition: box-shadow 0.5s;

      &-left {
        left: 0;

        &:hover:not(.swiper-button-disabled) {
          box-shadow: inset -32px 0 0 0 #a69c87;
        }
      }

      &-right {
        right: 0;

        &:hover:not(.swiper-button-disabled) {
          box-shadow: inset 32px 0 0 0 #a69c87;
        }
      }
    }
  }

  &-pagination {
    &-bullet {
      border-radius: 0px;
      width: 14px;
      height: 4px;
      margin: 10px 5px;
      background-color: #E2E6E9;
      opacity: 1;
      cursor: pointer;
      transition: transform 0.3s ease-in-out;

      &:hover:not(.swiper-pagination-bullet-active) {
        transform: scale(1.3);
      }

      &-active {
        background-color: #0F0F11;
      }
    }
  }
}

@media (max-width: 639px) {
  .swiper {
    &-slide {
      display: block;

      &__img {
        display: none;
      }

      &__img-mobile {
        display: block;
        width: 100%;
        height: 100%;
        margin: 0;
        object-fit: cover;
      }

      &__img-box {
        padding-inline: 0px;
        margin-inline: 0px;
      }

      &__main-arrow {
        display: none;
      }
    }

    &-pagination {
      &-bullet {
        bottom: 0;
        top: 0;
      }
    }
  }
}


